import { resetState } from 'store/ui/uiActions';
import { getType } from 'typesafe-actions';
import { invalidateSession, isCostTypeAvailable, isCurrencyAvailable, setAccountCurrency, setCostType, setCurrency, } from 'utils/localStorage';
import { fetchAccountSettingsFailure, fetchAccountSettingsRequest, fetchAccountSettingsSuccess, } from './accountSettingsActions';
export const defaultState = {
    byId: new Map(),
    errors: new Map(),
    fetchStatus: new Map(),
};
export function accountSettingsReducer(state = defaultState, action) {
    switch (action.type) {
        case getType(resetState):
            state = defaultState;
            return state;
        case getType(fetchAccountSettingsRequest):
            return Object.assign(Object.assign({}, state), { fetchStatus: new Map(state.fetchStatus).set(action.payload.reportId, 1 /* inProgress */) });
        case getType(fetchAccountSettingsSuccess):
            initCostType(action.payload.data.cost_type);
            initCurrency(action.payload.data.currency);
            return Object.assign(Object.assign({}, state), { fetchStatus: new Map(state.fetchStatus).set(action.meta.reportId, 2 /* complete */), byId: new Map(state.byId).set(action.meta.reportId, Object.assign({}, action.payload)), errors: new Map(state.errors).set(action.meta.reportId, null) });
        case getType(fetchAccountSettingsFailure):
            return Object.assign(Object.assign({}, state), { fetchStatus: new Map(state.fetchStatus).set(action.meta.reportId, 2 /* complete */), errors: new Map(state.errors).set(action.meta.reportId, action.payload) });
        default:
            return state;
    }
}
// Initialize cost type in local storage
function initCostType(value) {
    // Clear local storage value if current session is not valid
    invalidateSession();
    if (!isCostTypeAvailable()) {
        setCostType(value);
    }
}
// Initialize currency in local storage
function initCurrency(value) {
    // Clear local storage value if current session is not valid
    invalidateSession();
    if (!isCurrencyAvailable()) {
        setCurrency(value);
    }
    setAccountCurrency(value);
}
