import { getReportId, stateKey } from './accountSettingsCommon';
export const selectAccountSettingsState = (state) => state[stateKey];
// Fetch account settings
export const selectAccountSettings = (state) => selectAccountSettingsState(state).byId.get(getReportId());
export const selectAccountSettingsFetchStatus = (state) => selectAccountSettingsState(state).fetchStatus.get(getReportId());
export const selectAccountSettingsError = (state) => selectAccountSettingsState(state).errors.get(getReportId());
