import { getQuery } from 'api/queries/awsQuery';
import { getCostType } from 'utils/costType';
export const awsDashboardStateKey = 'awsDashboard';
export const awsDashboardDefaultFilters = {
    time_scope_units: 'month',
    time_scope_value: -1,
    resolution: 'daily',
};
export const awsDashboardTabFilters = Object.assign(Object.assign({}, awsDashboardDefaultFilters), { limit: 3 });
export function getGroupByForTab(widget) {
    switch (widget.currentTab) {
        case "services" /* services */:
            // Use group_by for service tab and filter for others -- https://github.com/project-koku/koku-ui/issues/846
            return {
                service: widget.tabsFilter && widget.tabsFilter.service ? widget.tabsFilter.service : '*',
            };
        case "accounts" /* accounts */:
            return { account: '*' };
        case "regions" /* regions */:
            return { region: '*' };
        default:
            return {};
    }
}
export function getQueryForWidget(widget, filter = awsDashboardDefaultFilters, props) {
    const query = Object.assign(Object.assign({ filter }, (widget.savingsPlan && { cost_type: getCostType() })), (props ? props : {}));
    return getQuery(query);
}
export function getQueryForWidgetTabs(widget, filter = awsDashboardDefaultFilters, props) {
    const group_by = getGroupByForTab(widget);
    const newFilter = Object.assign({}, JSON.parse(JSON.stringify(filter)));
    // Use group_by for service tab and filter for others -- https://github.com/project-koku/koku-ui/issues/846
    if (widget.currentTab === "services" /* services */ && widget.tabsFilter && widget.tabsFilter.service) {
        newFilter.service = undefined;
    }
    const query = Object.assign(Object.assign(Object.assign({}, (widget.savingsPlan && { cost_type: getCostType() })), { filter: newFilter, group_by }), (props ? props : {}));
    return getQuery(query);
}
